<template>
	<view class="soupItem">
		<view class="head">
			<view class="left">
				<self-tab-group :status="item.status"></self-tab-group>
				<text class="status-text">{{ useStateFormat }}</text>
			</view>
			<view class="right" @click.stop="goDetail">
				<uni-icons type="eye" size="18" color="#999"></uni-icons>
				<text>详情</text>
			</view>
		</view>
		
		<view class="content" @click="goDetail">
			<text>{{ item.content || '暂无内容' }}</text>
		</view>
		
		<!-- 图片展示 -->
		<view class="images" v-if="item.pics && item.pics.length > 0">
			<image 
				v-for="(pic, index) in item.pics" 
				:key="index"
				:src="pic" 
				mode="aspectFill"
				@click.stop="previewImage(index)"
				class="content-image"
			></image>
		</view>
		
		<view class="info">
			<view class="left">
				<view class="time">
					{{ formatTime(item.publish_date || item.createTime) }}
				</view>
			</view>
			<view class="right">
				<view class="group">
					<view class="every" @click.stop="toggleLike">
						<uni-icons 
							:type="isLiked ? 'heart-filled' : 'heart'" 
							:color="isLiked ? '#ff5652' : '#a7a7a7'" 
							size="16"
						></uni-icons>
						<text class="font">{{ item.like_count || 0 }}</text>
					</view>
					<view class="every" @click.stop="toggleFavorite">
						<uni-icons 
							:type="isFavorited ? 'star-filled' : 'star'" 
							:color="isFavorited ? '#ffc107' : '#a7a7a7'" 
							size="16"
						></uni-icons>
						<text class="font">{{ item.favorite_count || 0 }}</text>
					</view>
					<view class="every" @click.stop="goDetail">
						<uni-icons type="chatbubble" color="#a7a7a7" size="16"></uni-icons>
						<text class="font">{{ item.comment_count || 0 }}</text>
					</view>
				</view>
				
				<view class="actions" v-if="showActions && isOwn">
					<uni-icons 
						type="more" 
						size="20" 
						color="#999"
						@click.stop="showActionSheet"
					></uni-icons>
				</view>
			</view>
		</view>
	</view>
</template>

<script setup>
import { ref, computed, onMounted } from "vue"

const props = defineProps({
	item: {
		type: Object,
		default() {
			return {}
		}
	},
	// 当前用户ID，用于判断收藏点赞状态
	currentUserId: {
		type: String,
		default: ''
	}
})

const emit = defineEmits(['delete', 'refresh'])

// 响应式数据
const isLiked = ref(false)
const isFavorited = ref(false)
const showActions = ref(true)

// 计算属性 - 判断是否是自己的内容
const isOwn = computed(() => {
	return props.currentUserId && props.item.user_id === props.currentUserId
})

// 状态格式化函数
const stateFormat = (status) => {
    const statusMap = {
        0: '草稿',
        1: '已发布',
        2: '审核中',
        3: '审核失败',
        4: '已删除'
    }
    return statusMap[status] || '未知状态'
}

// 计算属性
const useStateFormat = computed(() => stateFormat(props.item.status))

// 格式化时间
const formatTime = (timestamp) => {
	if (!timestamp) return ''
	
	try {
		const date = new Date(timestamp)
		const now = new Date()
		const diff = now - date
		
		// 如果是今天内
		if (diff < 24 * 60 * 60 * 1000) {
			const hours = date.getHours().toString().padStart(2, '0')
			const minutes = date.getMinutes().toString().padStart(2, '0')
			return `${hours}:${minutes}`
		}
		
		// 如果是今年
		if (date.getFullYear() === now.getFullYear()) {
			const month = (date.getMonth() + 1).toString().padStart(2, '0')
			const day = date.getDate().toString().padStart(2, '0')
			return `${month}-${day}`
		}
		
		// 其他情况
		return `${date.getFullYear()}-${(date.getMonth() + 1).toString().padStart(2, '0')}-${date.getDate().toString().padStart(2, '0')}`
	} catch (error) {
		console.error('时间格式化错误:', error)
		return ''
	}
}

// 初始化收藏点赞状态
const initInteractionStatus = async () => {
	if (!props.item?._id || !props.currentUserId) return
	
	try {
		// 检查收藏状态
		const favoriteRes = await uniCloud.importObject('articlesCloudObj').checkFavorite({
			articleId: props.item._id,
			userId: props.currentUserId
		})
		
		if (favoriteRes.errCode === 0) {
			isFavorited.value = favoriteRes.data.isFavorited
		}
		
	} catch (error) {
		console.error('初始化交互状态失败:', error)
	}
}

// 图片预览
const previewImage = (index) => {
	if (!props.item?.pics || props.item.pics.length === 0) return
	
	uni.previewImage({
		urls: props.item.pics,
		current: index
	})
}

// 点赞/取消点赞
const toggleLike = async () => {
	if (!props.item?._id) return
	
	try {
		console.log('toggleLike:', props.item._id)
		
		// 临时前端状态切换
		isLiked.value = !isLiked.value
		if (isLiked.value) {
			props.item.like_count = (props.item.like_count || 0) + 1
		} else {
			props.item.like_count = Math.max(0, (props.item.like_count || 1) - 1)
		}
		
	} catch (error) {
		console.error('点赞操作失败:', error)
		uni.showToast({
			title: '操作失败',
			icon: 'none'
		})
	}
}

// 收藏/取消收藏
const toggleFavorite = async () => {
	if (!props.item?._id || !props.currentUserId) {
		uni.showToast({
			title: '请先登录',
			icon: 'none'
		})
		return
	}
	
	try {
		const articlesCloudObj = uniCloud.importObject('articlesCloudObj')
		
		if (isFavorited.value) {
			// 取消收藏
			const res = await articlesCloudObj.removeFavorite({
				articleId: props.item._id,
				userId: props.currentUserId
			})
			
			if (res.errCode === 0) {
				isFavorited.value = false
				props.item.favorite_count = Math.max(0, (props.item.favorite_count || 1) - 1)
				uni.showToast({
					title: '已取消收藏',
					icon: 'success'
				})
			}
		} else {
			// 添加收藏
			const res = await articlesCloudObj.addFavorite({
				articleId: props.item._id,
				userId: props.currentUserId
			})
			
			if (res.errCode === 0) {
				isFavorited.value = true
				props.item.favorite_count = (props.item.favorite_count || 0) + 1
				uni.showToast({
					title: '收藏成功',
					icon: 'success'
				})
			} else {
				uni.showToast({
					title: res.errMsg || '收藏失败',
					icon: 'none'
				})
			}
		}
		
	} catch (error) {
		console.error('收藏操作失败:', error)
		uni.showToast({
			title: '操作失败',
			icon: 'none'
		})
	}
}

// 查看详情（包含评论功能）
const goDetail = () => {
	console.log("跳转详情:", props.item?._id)
	if (!props.item?._id) return
	
	uni.navigateTo({
		url: `/pages/blog/detail?id=${props.item._id}`
	})
}

// 显示操作菜单 - 只有删除功能
const showActionSheet = () => {
	uni.showActionSheet({
		itemList: ['删除'],
		success: (res) => {
			if (res.tapIndex === 0) {
				confirmDelete()
			}
		}
	})
}

// 确认删除
const confirmDelete = () => {
	uni.showModal({
		title: '确认删除',
		content: '确定要删除这条内容吗？',
		success: (res) => {
			if (res.confirm) {
				deleteItem()
			}
		}
	})
}

// 删除项目
const deleteItem = async () => {
	if (!props.item?._id) return
	
	try {
		const articlesCloudObj = uniCloud.importObject('articlesCloudObj')
		const res = await articlesCloudObj.remove(props.item._id)
		
		if (res.errCode === 0) {
			uni.showToast({
				title: '删除成功',
				icon: 'success'
			})
			emit('delete', props.item._id)
			emit('refresh')
		} else {
			uni.showToast({
				title: '删除失败',
				icon: 'none'
			})
		}
	} catch (error) {
		console.error('删除失败:', error)
		uni.showToast({
			title: '删除失败',
			icon: 'none'
		})
	}
}

// 生命周期
onMounted(() => {
	initInteractionStatus()
})
</script>

