				margin-left: 10rpx;
				font-size: 24rpx;
				color: $text-font-color-3;
			}
		}
		.right{
			font-size: 28rpx;
			color:$text-font-color-3;
			display: flex;
			align-items: center;			
			padding:5rpx 10rpx;
			border-radius: 20rpx;
			background: #f5f5f5;
			text{
				margin-left:10rpx;
				line-height: 1em;
			}
		}
	}
	
	.content{
		padding:30rpx 20rpx;
		font-size: 40rpx;
		font-weight: bold;
		line-height: 1.7em;
		text-align: justify;
		color: #333;
		word-break: break-word;
	}
	
	.images {
		padding: 0 20rpx 20rpx;
		display: flex;
		flex-wrap: wrap;
		gap: 10rpx;
		
		.content-image {
			width: 200rpx;
			height: 200rpx;
			border-radius: 10rpx;
