		}
	}
	
	.info{
		background: #f9f9f9;
		padding:20rpx;
		display: flex;
		align-items: center;
		justify-content: space-between;
		color:$text-font-color-3;
		font-size: 24rpx;
		
		.left {
			.time {
				color: #999;
			}
		}
		
		.right {
			display: flex;
			align-items: center;
			gap: 20rpx;
			
			.group{
				display: flex;
				align-items: center;
				.every{
					margin-left:20rpx;
					display: flex;
					align-items: center;
					gap: 8rpx;
					
					.font{					
						font-size: 26rpx;
					}
				}
			}
			
			.actions {
				padding: 8rpx;
				border-radius: 6rpx;
				&:active {
					background: #eaeaea;
				}
			}
		}
	}
}
</style>